import React from "react";

export default function FooterLinks({ footerHandle, arrFoot, clickIndex }) {
  return (
    <section className="footer-links">
      {arrFoot.map((column, index) => (
        <div key={index} className={`footer-column cul${index + 1}`}>
          {Object.keys(column).map((key) =>
            column[key].map((section) => (
              <div key={section.id} className="footer-section">
                <h3 onClick={() => footerHandle(section.class)}>
                  {section.id}
                </h3>
                <ul
                  className={`ul-${
                    section.class == clickIndex
                      ? `footeOpen  ul-${section.class}`
                      : ""
                  }`}
                >
                  {section.list.map((item) => (
                    <li key={item.title}>
                      <a href={item.link}>{item.title}</a>
                    </li>
                  ))}
                </ul>
              </div>
            ))
          )}
        </div>
      ))}
    </section>
  );
}
